<template>
    <div class="list">
     
          <p
            v-for="movies in movie"
            :key="movies.id"
          >
        <a :href="movies.url ==''?'###':movies.url">  <img width="100%" v-lazy="movies.images" alt=""> </a>
         </p>
           
        
    </div>
</template>

<script>

export default {
    data(){
        return{
            movie:[]
        }
    },
    created() {
        //获取传递来的id
        
        this.$http.get("/api/mz/zixun/").then(res=>{
            this.movie = res.data.data.object_list;
            // console.log(res.data)
        })
    },
  
    
}
</script>


<style lang="scss">
    .list{
        margin-top:15px
    }
</style>
